body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.headContainer {
  display: flex;
  justify-content: space-between;
  background-color: #e8fce8;
  color: darkgreen;
  font-family: sans-serif; }

.menu {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 25px; }

.logoContainer {
  display: flex;
  align-items: center; }

.logo {
  padding: 30px; }

.menuElement {
  display: inline;
  padding: 15px;
  font-weight: bolder;
  font-size: 18px;
  -webkit-transition: 0.5s;
  transition: 0.5s; }

.menuElement:hover {
  background-color: darkorange;
  border-radius: 20px;
  -webkit-transition: border-radius 0.8s;
  transition: border-radius 0.8s; }

.bannerTitle {
  background: lightgreen;
  text-align: center;
  font-family: sans-serif;
  color: saddlebrown;
  padding: 40px;
  font-size: 32px;
  font-weight: bold; }

.bannerContainer {
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(50%, rgba(0, 0, 0, 0.6)), to(rgba(0, 0, 0, 0.2))), url(/Birtday_App/static/media/boy-playing-with-fall-leaves-outdoors-36965.77cf5315.jpg);
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.6) 50%, rgba(0, 0, 0, 0.2) 100%), url(/Birtday_App/static/media/boy-playing-with-fall-leaves-outdoors-36965.77cf5315.jpg);
  height: 700px;
  background-size: cover;
  position: relative; }

.bannerContainer:hover {
  background-image: url(/Birtday_App/static/media/boy-playing-with-fall-leaves-outdoors-36965.77cf5315.jpg); }

.text {
  background: darkseagreen;
  width: 420px;
  padding: 40px;
  top: 90px;
  left: 90px;
  position: absolute;
  opacity: 0.9;
  color: darkgreen;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 5px;
  box-sizing: border-box;
  -webkit-transition: 1s;
  transition: 1s;
  line-height: 130%;
  font-size: 18px;
  font-family: sans-serif; }

.text:hover {
  -webkit-transform: scale(1.3) rotate(3deg);
          transform: scale(1.3) rotate(3deg);
  border: 8px solid saddlebrown; }

.title {
  color: saddlebrown;
  white-space: nowrap; }

.button {
  border-radius: 25px;
  border: none;
  padding: 15px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  text-align: center;
  font-size: 22px; }

.button:hover {
  background: saddlebrown;
  color: oldlace;
  opacity: 1; }

.slider {
  width: 70vw;
  height: 600px;
  position: relative;
  margin: auto; }

.slide {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 0; }

.leftArrow {
  position: absolute;
  top: 50%;
  padding: 0; }

.rightArrow {
  position: absolute;
  top: 50%;
  right: 0;
  padding: 0; }

.galleryTitle {
  background: darkcyan;
  text-align: center;
  font-family: sans-serif;
  color: greenyellow;
  padding: 30px;
  font-size: 32px;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(1, 44, 0, 0.9);
  position: relative;
  z-index: 1; }

.galleryTitle::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: darkcyan;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  -webkit-transform: skewY(-2deg);
          transform: skewY(-2deg);
  z-index: -10; }

.galleryTitle::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: darkcyan;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-transform: skewY(1deg);
          transform: skewY(1deg);
  z-index: -10; }

body {
  color: #49332B;
  font-family: 'Fredoka+One', 'sans-serif'; }

.mainContainer {
  background-size: 80%;
  background: rgba(0, 0, 0, 0.1) url(/Birtday_App/static/media/ramin-talebi-u_b0jWnkgM0-unsplash.7416ae93.jpg) no-repeat center;
  height: 700px;
  position: relative;
  padding: 25px;
  font-size: 16px;
  font-family: sans-serif;
  display: flex; }

.basicOffer {
  grid-area: main; }

.extraOffer {
  grid-area: main; }

.calcForm {
  grid-area: side; }

.offerContainer {
  text-align: left; }

.calcContainer {
  background: darkseagreen;
  padding: 30px;
  opacity: 0.9;
  border-radius: 5px;
  width: 84vw;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "main side" "main side";
  grid-gap: 40px;
  margin: 20px 100px;
  line-height: 130%;
  height: 600px; }

.calcTitle {
  background: darkcyan;
  text-align: center;
  font-family: sans-serif;
  color: greenyellow;
  padding: 40px;
  font-size: 32px;
  font-weight: bold;
  text-shadow: 1px 1px 1px rgba(1, 44, 0, 0.9);
  position: relative;
  z-index: 1; }

.calcTitle::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: darkcyan;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  -webkit-transform: skewY(-2deg);
          transform: skewY(-2deg);
  z-index: -10; }

.calcTitle::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: darkcyan;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-transform: skewY(1deg);
          transform: skewY(1deg);
  z-index: -10; }

.additionalOptions {
  display: flex;
  flex-direction: column; }

.headLines {
  margin: 20px 0 10px 0; }

.info {
  margin: 10px 0;
  font-size: 14px; }

.offerInfo {
  font-size: 18px;
  text-align: center;
  padding: 20px;
  background: #49332B;
  color: white;
  border-radius: 5px;
  width: 70%;
  margin: 0 auto; }

.radioContainer {
  display: flex;
  justify-content: center; }

.radioBtn {
  margin: 10px; }

.optionsContainer {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-left: 100px; }

.checkbox {
  padding-top: 10px; }

